
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sideBar {
	margin-top: 20px;
	width: 100%;
	padding: 10px;
	background-color: #d2d1d2;
	height: 80%;
}
.lambdaTitle {
	text-decoration: none;
	color: black;
}
.sideBarButtons {
	display: flex;
	flex-direction: column;
	height: 100px;
	justify-content: space-between;
	text-decoration: none;
}
.sideBarButtons button {
	padding: 20px 0;
	background-color: #00b8ba;
	color: white;
	font-size: 16px;
	font-weight: bold;
}
.container .notesBody {
	padding: 20px;
}

.notePageTitle {
    width: 100%;
    padding-left: 20px;
}
.allNotes {
    width: 80%;
    border: 1px solid darkgray;
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: space-around;
    background-color: #F2F0F1;
}
.note {
    border: 1px solid darkgray;
    padding: 20px;
    height: 500px;
    width: 400px;
    margin: 20px 0;
    background-color: white;
}
.noteTitle {
    text-decoration: none;
    color: black;
    border-bottom: 1px solid black;
    padding-bottom: 20px;
}
.noteText {
    text-overflow: ellipsis;
    height: 395px;
    overflow: hidden;
}
.createNote {
    width: 80%;
    padding: 50px;
    background-color: #F2F0F1;
}
.createNote .createNoteHeader {
    font-size: 24px;
}
.createNote .createNoteForm {
    display: flex;
    flex-wrap: wrap;
}
.createNote .newTitle {
    width: 40%;
    padding: 5px 0 5px 10px;
    margin-bottom: 20px;
}
.createNote .newTextBody {
    width: 100%;
    padding: 10px 0 400px 10px;
    margin-bottom: 20px;
}
.createNote .newTextBody input.break {
    word-wrap: break-word;
    word-break: break-all;
}
.createNote button {
    width: 200px;
    height: 50px;
    background-color: #00B8BA;
    color: white;
    font-weight: bold;
    font-size: 18px;
    border: none;
}
.deleteModal {
    z-index: 1;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: black;
    opacity: 0.5;
    color: black;
    text-align: center;
    padding-top: 50%;
}
.deleteModal .modalBox {
    height: 200px;
    width: 50%;
    position: fixed;
    left: 25%;
    top: 50%;
    border: 1px solid pink;
    padding-top: 100px;
    background-color: white;
}
.deleteModal .modalBox .modalButtons {
    display: flex;
    justify-content: space-evenly;
    padding: 20px 0;
}

.deleteModal .modalBox .modalButtons button {
    width: 200px;
    height: 50px;
}
.activeNote {
    padding: 20px 50px;
    width: 80%;
    background-color: #F2F0F1;
}
.activeNote .noteContent {
    line-height: 2.5;
    margin: 20px 0;
}
.activeNote .noteContent h3 {
    font-size: 24px;
}
.activeNote .noteContent p {
    font-size: 18px;
}
.activeNote .buttons {
    display: flex;
    justify-content: flex-end;
    width: 75%;
    margin: 0 auto;
}

.activeNote .buttons button {
    text-decoration: underline;
    margin-left: 20px;
    border: none;
    background-color: #F2F0F1;
    font-weight: bold;
}
.searchPage {
    width: 80%;
    margin-top: 20px;
}
.searchBar {
    width: 50%;
    height: 50px;
    padding-left: 20px;
}
.searchedNote a {
    text-decoration: none;
    color: black;
}
.searchedNote {
    padding: 10px 0px;
}
.App {
	display: flex;
	justify-content: space-around;
}

